.nav-bar {
    display: grid;
    grid-template-columns: 20% 80%;
    justify-content: end;
    background-color: rgb(70, 90, 70);
    padding: .5em;
    margin-bottom: 2em;
}

.right-menu {
    justify-self: end;
}

.logout {
    cursor: pointer;
    display: inline-block;
    min-height: 1em;
    outline: 0;
    border: none;
    vertical-align: baseline;
    background: #e0e1e2 none;
    color: rgba(0,0,0,.6);
    font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
    margin: 0 .25em 0 0;
    padding: .78571429em 1.5em .78571429em;
    text-transform: none;
    text-shadow: none;
    font-weight: 700;
    line-height: 1em;
    font-style: normal;
    text-align: center;
    text-decoration: none;
    border-radius: .28571429rem;
}

